          border>
          <el-table-column
            align="center"
            label="序号"
            prop="date">
          </el-table-column>
          <el-table-column
            align="center"
            label="标题"
            prop="name">
          </el-table-column>
          <el-table-column
            align="center"
            label="发送时间"
            prop="name">
          </el-table-column>
          <el-table-column
            align="center"
            label="发送人"
            prop="name">
          </el-table-column>
          <el-table-column
